}

.actioncall {
  border: solid black 1px;
  position: absolute;
  right: 5vw; }
  .actioncall > li {
    color: black; }

.nav > ul {
  display: -webkit-flex;
  display: flex;
  list-style-type: none;
  margin: 0;
  background-color: transparent;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  width: 50vw;
  margin-top: 20px;
  -webkit-animation: mymove 2s;
          animation: mymove 2s; }
  .nav > ul :hover {
    color: blue; }
  .nav > ul > a {
    padding: 16px;
    text-decoration: none; }

@-webkit-keyframes mymove {
  0% {
    opacity: 0;
    margin: 0;
    color: white; }
  100% {
    opacity: 1;
    color: black; } }

@keyframes mymove {
  0% {
    opacity: 0;
    margin: 0;
    color: white; }
  100% {
    opacity: 1;
    color: black; } }

body {
