  top: 20vh;
  left: 3vw;
  -webkit-animation: titel 2s;
          animation: titel 2s; }

p {
  top: 25vh; }

@-webkit-keyframes mymove {
  0% {
    opacity: 0;
    margin: 0;
    color: white; }
  100% {
    opacity: 1;
    color: black; } }

@keyframes mymove {
  0% {
    opacity: 0;
    margin: 0;
    color: white; }
  100% {
    opacity: 1;
    color: black; } }

@-webkit-keyframes image {
  0% {
    opacity: 0;
    right: -20px;
    bottom: 0; }
  100% {
    opacity: 1; } }

@keyframes image {
  0% {
    opacity: 0;
    right: -20px;
    bottom: 0; }
  100% {
    opacity: 1; } }

@-webkit-keyframes titel {
  0% {
    opacity: 0;
    left: 5vw; }
  100% {
    opacity: 1; } }

@keyframes titel {
  0% {
    opacity: 0;
    left: 5vw; }
  100% {
    opacity: 1; } }

.glitch {
  text-transform: uppercase;
  margin: 0;
  color: #000;
  letter-spacing: 5px; }
  .glitch:before, .glitch:after {
    display: block;
    content: attr(data-glitch);
    text-transform: uppercase;
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    opacity: .8; }
  .glitch:after {
    color: #3A302E;
    z-index: -2; }
  .glitch:before {
    color: #1D2C8A;
    z-index: -1; }
  .glitch:hover:before {
    -webkit-animation: glitch 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94) both 1;
            animation: glitch 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94) both 1; }
  .glitch:hover:after {
    animation: glitch 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94) reverse both 1; }

@-webkit-keyframes glitch {
  0% {
    -webkit-transform: translate(0);
            transform: translate(0); }
  60% {
    -webkit-transform: translate(5px, 5px);
            transform: translate(5px, 5px); }
  80% {
    -webkit-transform: translate(5px, -5px);
            transform: translate(5px, -5px); }
  to {
    -webkit-transform: translate(0);
            transform: translate(0); } }

@keyframes glitch {
  0% {
    -webkit-transform: translate(0);
            transform: translate(0); }
  60% {
    -webkit-transform: translate(5px, 5px);
            transform: translate(5px, 5px); }
  80% {
    -webkit-transform: translate(5px, -5px);
            transform: translate(5px, -5px); }
  to {
    -webkit-transform: translate(0);
            transform: translate(0); } }






